import React, { Component } from 'react'

export default class Work extends Component {
    render() {
        return (

            <section className="colorlib-work" data-section="work">
                <div className="colorlib-narrow-content">
                    <div className="row">
                        <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                            <span className="heading-meta">My Work</span>
                            <h2 className="colorlib-heading animate-box">Recent react practice work</h2>
                        </div>
                    </div>
                    
                    <div className="row">
                        <div className="col-md-6 animate-box" data-animate-effect="fadeInLeft">
                            <div className="project" style={{ backgroundImage: 'url(images/img-1.jpg)' }}>
                                <div className="desc">
                                    <div className="con">
                                        <h3><a href="http://hangxuanyuan.github.io/react-learning">Work 01:To Do List app</a></h3>
                                        <span>To Do list app</span>
                                        <p className="icon">
                                            <span><a href="http://hangxuanyuan.github.io/react-learning"><i className="icon-share3" /></a></span>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6 animate-box" data-animate-effect="fadeInRight">
                            <div className="project" style={{ backgroundImage: 'url(images/img-2.jpg)' }}>
                                <div className="desc">
                                    <div className="con">
                                        <h3><a href="http://hangxuanyuan.github.io/react-practiceform">Work 02: Registration Form</a></h3>
                                        <span>a sample registration form</span>
                                        <p className="icon">
                                            <span><a href="http://hangxuanyuan.github.io/react-practiceform"><i className="icon-share3" /></a></span>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}