import React, { Component } from 'react'

export default class Experience extends Component {
    render() {
        return (

            <section className="colorlib-experience" data-section="experience">
                <div className="colorlib-narrow-content">
                    <div className="row">
                        <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                            <span className="heading-meta">Experience</span>
                            <h2 className="colorlib-heading animate-box">Work and Project Experience</h2>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12">
                            <div className="timeline-centered">
                                <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                                    <div className="timeline-entry-inner">
                                        <div className="timeline-icon color-1">
                                            <i className="icon-pen2" />
                                        </div>
                                        <div className="timeline-label">
                                            <h2><a href="#">Development of Mobile App for Yamaf Teakwondo Club</a> <span>Apr 2017 - Jul 2017</span></h2>
                                            <p>this is an employer project that require us to develop a mobile app which is for educational purpose for the teakwondo club.</p>
                                        </div>
                                    </div>
                                </article>
                                <article className="timeline-entry animate-box" data-animate-effect="fadeInRight">
                                    <div className="timeline-entry-inner">
                                        <div className="timeline-icon color-2">
                                            <i className="icon-pen2" />
                                        </div>
                                        <div className="timeline-label">
                                            <h2><a href="#">Research in Digital Marketing Strategies for Fuji Xerox Asia Pacific Lte Ptd</a> <span>Aug 2017- Nov 2017</span></h2>
                                            <p>this is another employer project that took place in another semester where we need to came out with the ideas that utilize IT technology to help Fuji Xerox to increase thier sales lead.</p>
                                        </div>
                                    </div>
                                </article>
                                <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                                    <div className="timeline-entry-inner">
                                        <div className="timeline-icon color-3">
                                            <i className="icon-pen2" />
                                        </div>
                                        <div className="timeline-label">
                                            <h2><a href="#">E-Commerce Web Administrator internship</a> <span>Jan 2018 - Mar 2018</span></h2>
                                            <p>During this internship, my job scope is to improve and manage the existing company website and also existing stores in multiple e-commerce platforms. Furthermore, i also took part to become a graphic designer for the company with the experience i have for using Adobe Photoshop.</p>
                                        </div>
                                    </div>
                                </article>
                                <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                                    <div className="timeline-entry-inner">
                                        <div className="timeline-icon color-4">
                                            <i className="icon-pen2" />
                                        </div>
                                        <div className="timeline-label">
                                            <h2><a href="#">Development of Course Management System for Lifesaving Society of Malaysia</a> <span>July 2018 - Nov 2018</span></h2>
                                            <p>this is a employer project from Lifesaving Society of Malaysia to develop a CMS to help them manage thier course. this is a web-based system created with PHP and SQL. i handled most of the development of the backend of the system.</p>
                                        </div>
                                    </div>
                                </article>
                                <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                                    <div className="timeline-entry-inner">
                                        <div className="timeline-icon color-5">
                                            <i className="icon-pen2" />
                                        </div>
                                        <div className="timeline-label">
                                            <h2><a href="#">Development of Travel Agency Content Management System</a> <span>Aug 2019 - Jul 2020</span></h2>
                                            <p>this is my final year project where we developed a website with WordPress requested by client. </p>
                                        </div>
                                    </div>
                                </article>
                                <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                                    <div className="timeline-entry-inner">
                                        <div className="timeline-icon color-none">
                                        </div>
                                    </div>
                                </article>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}