import React, { Component } from 'react'

export default class About extends Component {
    render() {
        return (
            <div>
                <section className="colorlib-about" data-section="about">
                    <div className="colorlib-narrow-content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInLeft">
                                    <div className="col-md-12">
                                        <div className="about-desc">
                                            <span className="heading-meta">About Us</span>
                                            <h2 className="colorlib-heading">Who Am I?</h2>
                                            <p><strong>Hi I'm Hang Xuan Yuan</strong>. I just recently graduated from University of Wollongong with Software Engineering as my major. I'm trying to find a job. i created this website as a practice for me to know more about <strong>React Js</strong>.</p>
                                            <p>As i begin to learn more about React Js, i found out that i could do alot of things with this language. so i decided to develop a website by trying to convert all the HTML element from this free template into JSX so that i can deploy it and code it in React Environment.</p>
                                            <p>I will use this website as the main website to contain all my work done during this short learning session.</p>
                                        </div>
                                    </div>
                                </div>


                                
                            </div>
                        </div>
                    </div>
                </section>

                <section className="colorlib-about" >
                    <div className="colorlib-narrow-content">
                        <div className="row">
                            <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                                <span className="heading-meta">What I do?</span>
                                <h2 className="colorlib-heading">Here are some of my expertise</h2>
                            </div>
                        </div>
                        <div className="row row-pt-md">
                            <div className="col-md-4 text-center animate-box">
                                <div className="services color-1">
                                    <span className="icon">
                                        <i className="icon-bulb" />
                                    </span>
                                    <div className="desc">
                                        <h3>Software Development</h3>
                                        <p>Windows application</p>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4 text-center animate-box">
                                <div className="services color-2">
                                    <span className="icon">
                                        <i className="icon-data" />
                                    </span>
                                    <div className="desc">
                                        <h3>Web Development</h3>
                                        <p>Web-Based System and Websites </p>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4 text-center animate-box">
                                <div className="services color-3">
                                    <span className="icon">
                                        <i className="icon-phone3" />
                                    </span>
                                    <div className="desc">
                                        <h3>Graphic Design</h3>
                                        <p>Photoshop and 3D-Modeling</p>
                                    </div>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                </section>
             </div>
		)
    }
}
